@use "variables";

//html font  ,


html {
    font-size:62.5%;
}

// PC  

@mixin width {
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
};

@mixin gnb-width {
    width: 100% - 10%;
    max-width: 1920px;
    margin: 0 auto;
};

// pc     ( 1200px)
@mixin contents-width {
    width: 1200px;
    max-width: 1920px;
    margin: 0 auto;
};

// 
@mixin align-center {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}
//   border 1px solid red
@mixin border {
    border:1px solid red;
    box-sizing: border-box;
}

//    container 
.container {
    @include width;
    position: relative;
}

//  

@mixin rwd-width {
    width: 100%;
    min-width: 320px;
    margin: 0 auto;
}

@mixin tablet {
 @media (max-width: 1199px) {
     @content;
 }
}

@mixin mobile {
    @media (max-width: 640px) {
        @content;
    }
}

@media (max-width: 1199px) {

    .container {
        @include rwd-width;
    }
}

